<script>
const axios = require("axios");

export default {
  name: "HelloWorld",
  data() {
    return {
      initialSearch: true,
      searchType: null,
      searchValue: null,
      practitionerIndex: null,
      practitioners: [],
      headers: [
        { text: "Name", sortable: true, value: "ProviderLongName" },
        { text: "NPI", sortable: true, value: "ProviderNpi" },
        { text: "STAR ID", sortable: true, value: "StarId" },
        { text: "NYS License", sortable: true, value: "NysLicense" },
        { text: "Primary Specialty", sortable: true, value: "PrimarySpecialty" }
      ]
    };
  },
  computed: {
    practitioner() {
      // get current practitioner
      return this.practitioners[this.practitionerIndex];
    }
  },
  methods: {
    reset: function() {
      this.searchValue = "";
    },
    search: function() {
      if (this.searchType && !this.searchValue) {
        // nothing entered
        return;
      }

      this.practitionerIndex = null;
      this.initialSearch = false;

      const url = "https://www030.chsli.org:3002/search";
      const qs = url + `?type=${this.searchType}&value=${this.searchValue}`;

      axios
        .get(qs)
        .then(response => {
          // retuns array
          this.practitioners = response.data;
          for (let i = 0; i < this.practitioners.length; i++) {
            this.practitioners[i].addresses = JSON.parse(
              this.practitioners[i].addresses
            );
          }

          if (this.practitioners.length === 1) {
            // just one row, so display
            this.practitionerIndex = 0;
          }
        })
        .catch(error => {
          console.log("error - ", error);
          // TODO - display error
        });
    },
    practitionerSelected: function(p) {
      // selection based on a few fields
      this.practitionerIndex = this.practitioners.findIndex(e => {
        return (
          e.PractitionerLongName == p.PractitionerLongName &&
          e.ProviderNpi == p.ProviderNpi &&
          e.NysLicense == p.NysLicense &&
          e.SCH_AdmitStatus == p.SCH_AdmitStatus &&
          e.SFH_AdmitStatus == p.SFH_AdmitStatus &&
          e.GSH_AdmitStatus == p.GSH_AdmitStatus &&
          e.MCH_AdmitStatus == p.MCH_AdmitStatus &&
          e.SJH_AdmitStatus == p.SJH_AdmitStatus &&
          e.MMC_AdmitStatus == p.MMC_AdmitStatus &&
          e.OLC_AdmitStatus == p.OLC_AdmitStatus &&
          e.GSP_AdmitStatus == p.GSP_AdmitStatus &&
          e.SCS_AdmitStatus == p.SCS_AdmitStatus
        );
      });
    }
  }
};
</script>

